const actions = {
  toggleFollow(state, id) {
    const accounts = state.accounts.map((f) => {
      if (f.id === id) {
        return { ...f, isFollow: !f.isFollow };
      } else {
        return f;
      }
    });
    return { ...state, accounts };
  },
};

